html{
  margin:0;
  padding:0;
  overflow: hidden;
}

body{
  position: absolute;
  width:100%;
  height:100%;
  overflow: auto;
}

.App {
  text-align: center;
  position: absolute;
  height: 100%;
  width: 100%;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.particlebg {
  background-color: black;
  position: absolute;
  height: 100%;
  width: 100%;
}

.eins {
  background: url('./pngtest.png') no-repeat center center;
  background-size: contain;
  position: absolute;
  top:20%;
  left:10%;
  width: 80%;
  height: 80%;
  z-index: 1;
  overflow: hidden;
  opacity: 1;
}

.drag {
  position: absolute;
  background-color: red;
  top:20%;
  left:10%;
  width: 8%;
  height: 8%;
  z-index: 1;
}

.zwei {
  background: url('./pngtest.png') no-repeat center center;
  position: absolute;
  top: 3%;
  left: 3%;
  width: 94%;
  height: 94%;
  background-color: blue;
  margin-left: 40px;
  margin-top: 40px;
  margin-bottom: 40px;
  margin-right: 180px;
  z-index: 2;
  overflow: hidden;
  opacity: 1;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



